<template>
  <div class="weather-wrapper">
    <ContentHeader />
    <CitySelector @selectCity="selectCity" />
    <!-- selectCity라는 이벤트로 부모가 호출되면 오른쪽 메서드를 호출하겠다는 의미! -->
    <WeatherList :weatherList="weatherList" />
  </div>
</template>

<script>
import weatherMixin from "@/mixins/weatherMixin";
import ContentHeader from "./ContentHeader";
import CitySelector from "./CitySelector";
import WeatherList from "./WeatherList";
export default {
  name: "WeatherContent",
  components: {
    ContentHeader,
    CitySelector,
    WeatherList,
  },
  mixins: [weatherMixin],
  data() {
    return {
      weatherList: [],
    };
  },
  methods: {
    async selectCity(city) {
      if (city.selected) {
        const weatehr = await this.getWeatherinfo(city);
        this.weatherList.push(weatehr);
      } else {
        const index = this.weatherList.findIndex(
          (weather) => weather.code === city.code
        );
        this.weatherList.splice(index, 1); //선택 index에서 1개를 빼겠다 = 고로 누를 자신이 빠짐.
      }
      // console.log(city, "received");
    },
  },
};
</script>

