<template>
  <li class="weather-item">
    <h3>{{ weather.label }}</h3>
    <p class="temperature">{{ weather.temperature }}&deg;c</p>
    <p class="info">
      <img :src="weather.icon" alt="any" />
      <span>습도 {{ weather.humidity }}</span>
      <span>바람 {{ weather.wind }}m/s</span>
    </p>
  </li>
</template>

<script>
export default {
  name: "weatherItem",
  props: {
    weather: {
      type: Object, //넘겨주는 데이터가 없어서 default
      default() {
        //넘겨받는 props가 objuct나 배열이면, 메서드 형태로 넘겨야함!
        return {
          label: "서울",
          code: "Seoul",
          temperature: 20,
          humidity: 15,
          wind: 1.5,
        };
      },
    },
  },
};
</script>

<style scoped>
.weather-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(238, 238, 238, 0.781);
  padding: 0.5em;
}

.weather-item h3 {
  font-size: 14px;
}
.weather-item .temperature {
  font-size: 36px;
}
.weather-item .info {
  display: flex;
  justify-content: center;
  align-content: center;
  font-size: 12px;
}

.weather-item .info * {
  margin-right: 0.4em;
}
</style>