<template>
  <div>
    <ul class="city-selector">
      <li
        @click="selectCity(city)"
        :class="{ isSelected: city.selected }"
        v-for="city in cities"
        :key="city.code"
      >
        {{ city.label }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "CitySelector",
  data() {
    return {
      cities: [
        { code: "Seoul", label: "서울", selected: false },
        { code: "Tokyo", label: "도쿄", selected: false },
        { code: "London", label: "런던", selected: false },
        { code: "Moscow", label: "모스크바", selected: false },
        { code: "Yakutsk", label: "야크추크", selected: false },
      ],
    };
  },
  methods: {
    selectCity(city) {
      city.selected = !city.selected;
      this.$emit("selectCity", city)
    },
  },
};
</script>

<style scoped>
.city-selector {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.city-selector li {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.05em solid rgba(247, 247, 247, 0.966);
  box-shadow: 1px 2px 8px rgba(236, 236, 236, 0.801);
  padding: 0.3em;
  cursor: pointer;
}
.city-selector li.isSelected {
  background: #ffd194;
}
</style>