import axios from 'axios'
//라이브러리를 노드 패키지에 저장했기 때문에 경로빼고 이름만 적어도 import됨.


const weatherMixin = {
    data() {
        return {};
    },
    methods: {
        async getWeatherinfo(city) {
            // console.log(process.env)
            const API_KEY = "768ee2ce75b75ea1ac96c76c014343f9"
            const url = `https://api.openweathermap.org/data/2.5/weather?q=${city.code}&appid=${API_KEY}`
            const res = await axios.get(url);
            const { main, wind, weather } = res.data;
            const weatherResult = {
                label: city.label,
                code: city.code,
                temperature: this.displayTemperature(main.temp),
                humidity: main.hunidity,
                wind: wind.speed,
                icon: `http://openweathermap.org/img/wn/${weather[0].icon}.png`,
            }
            // console.log(res)
            return weatherResult;
        },
        displayTemperature(temperature){
            return (temperature - 273.15).toFixed(1) //소숫점 한자리까지만!
        }
    },
}

export default weatherMixin